<template>
    <div class="newCouponCenter">
        <navigation-bar  @onLeftClick="onLeftClick" pageName="领券中心" ref="navigation"></navigation-bar>
        <van-tabs v-model="tabIndex" :swipeable="swipeable" animated>
            <van-tab ref="vantTab" title="店铺优惠券" color="#FB5768" line-width="0.67rem" line-height="0.08rem">
                <div class="scrollY" ref="scrollContent" v-if="tabIndex == 0">
                    <van-pull-refresh v-model="isRefreshLoading" @refresh="onRefresh">
                        <div v-if="shopCouponList.length>0">
                            <div class="topSpace"></div>
                            <coupon-item :lists="shopCouponList"/>
                            <div class="topSpace"></div>
                        </div>
                        <div class="noList" v-if="shopCouponList.length == 0&&!isLoading">
                            <img src="@/assets/images/default/no_coupon_img.png"/>
                            <div>暂无优惠券</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="平台红包" color="#FB5768" line-width="0.67rem" line-height="0.08rem">
                <div class="scrollY" ref="scrollContent" v-if="tabIndex == 1">
                    <van-pull-refresh v-model="isRefreshLoading" @refresh="onRefresh">
                        <div v-if="platformList.length>0">
                            <div class="topSpace"></div>
                            <coupon-item :lists="platformList"/>
                            <div class="topSpace"></div>
                        </div>
                        <div class="noList" v-if="platformList.length == 0&&!isPlatformListLoading">
                            <img src="@/assets/images/default/no_coupon_img.png"/>
                            <div>暂无优惠券</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import couponItem from '@/page/couponList/couponItem'
    import NetworkError from '@/common/networkError/NetworkError'
    import { queryCouponCenterPickupListService,queryPickupListV33Service, receiveBtnService } from '../../services/goodsService.js';
    import { Toast, Dialog,ActionSheet  } from "vant";
    export default {
        name:"newCouponCenter",
        components:{
            NavigationBar,
            couponItem,
            NetworkError
        },
        data(){
            return{
                swipeable:true,
                tabIndex:0,
                isNoNetwork:false, //是否没有网络
                isLoading:true, //是否在加载中
                isPlatformListLoading:true,

                isRefreshLoading:false,
                shopCouponList:[],
                platformList:[],
                scrollTop:0,
            }
        },
        created() {
            // if (this.$deciceType=='phone'){
            //     require('../../assets/css/phone/couponList/newCouponCenter.scss')
            // }else if (this.$deciceType=='portraitPad'){
            //     require('../../assets/css/portraitPad/couponList/newCouponCenter.scss')
            // }
        },
        activated(){
            this.$nextTick(()=>{
                let bodyHeight = document.documentElement.clientHeight || document.body.clientHeight;
                this.$refs.vantTab.$el.parentNode.parentNode.style.height = (bodyHeight-44-this.$stbHeight-50)+'px';
            });
            if(this.$route.params.isInit){
                this.tabIndex = 0;
                this.init();
            }else{
                this.$refs.scrollContent.scrollTop = this.scrollTop;
            }
        },
        deactivated(){

        },
        watch:{
            tabIndex:{
                handler(newName, oldName) {
                    this.init();
                },
            },
        },
        computed: {

        },
        methods:{
            //初始化数据
            init(){
                this.$refs.scrollContent.scrollTop = 0;
                this.platformList = [];
                this.shopCouponList = [];
                this.isLoading = true;
                this.isPlatformListLoading = true;
                this.isNoNetwork = false;
                if(this.tabIndex == 0){
                    this.queryPickupList();
                }else{
                    this.queryPickupListV33();
                }
            },




                        /**
             * 可领用的优惠券查询
             * @params
             * ownerCode    业主代码
             * areaCode     小区代码
             * */
             queryPickupList(){
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    areaCode: this.$userInfo.AREA_CODE
                }
                queryCouponCenterPickupListService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.isLoading = false;
                        this.isRefreshLoading = false;
                        this.shopCouponList = res.data.date;
                        this.shopCouponList.isShowBtn =  true;
                    }
                })
                .catch(error => {
                    this.shopCouponList = [];
                    this.isRefreshLoading = false;
                    this.isLoading = true;
                    this.isNoNetwork = true;
                });
            },
            /**
             * 可领用的优惠券查询
             * @params
             * ownerCode    业主代码
             * areaCode     小区代码
             * */
            queryPickupListV33(){
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    areaCode: this.$userInfo.AREA_CODE
                }
                queryPickupListV33Service(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.isPlatformListLoading = false;
                        this.isRefreshLoading = false;
                        this.platformList = res.data.platformList;
                        this.platformList.isShowBtn =  true;

                    }
                }).catch(error => {
                    this.platformList = [];
                    this.isRefreshLoading = false;
                    this.isPlatformListLoading = true;
                    this.isNoNetwork = true;
                });
            },
            //下拉刷新
            onRefresh(){
                this.init();
            },
            //返回键
            onLeftClick() {
                this.$router.go(-1);//返回上一层
            },
        },
        beforeRouteLeave(to, form, next) {
            this.scrollTop  = this.$refs.scrollContent.scrollTop;
            next();
        }
    }
</script>

<style scoped lang="scss">
    /deep/ .navBar {
        box-shadow: none;
    }

    .newCouponCenter {
        width: 100%;
        height: 100%;
        background-color: #f9f9f9;
        display: flex;
        flex-direction: column;
    }

    /deep/ .van-tabs {
        height: 100%;

        .van-tabs__wrap {
            padding: 0 50px;
            background: #FFFFFF;
            height: 45px !important;
            border: 0;

            .van-tab {
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #9A9A9A;
                letter-spacing: 0;
            }

            .van-tab--active {
                font-family: PingFangSC-Medium;
                color: #4A4A4A;
                font-weight: 600;
            }

            .van-tabs__line {
                width: 25px !important;
                height: 3px;
                border-radius: 4px;
                background: #17B7BD;
            }
        }

        .van-tabs__wrap::after {
            border-width: 0;
        }

        .van-tabs__content {
            height: 100%;

            .van-tabs__track {
                height: 100%;

                .van-tab__pane-wrapper {
                    height: 100%;

                    .van-tab__pane {
                        height: 100%;

                        .scrollY {
                            height: 100%;
                            overflow-y: auto;

                            .van-pull-refresh {
                                min-height: 100%;
                            }
                        }
                    }
                }
            }
        }
    }

    .topSpace {
        width: 100%;
        height: 10px;
    }

    .network {
        background: #f9f9f9;
    }

    .noList {
        img {
            margin: 125px 100px 10px 100px;
            width: 180px;
            display: inline-block;
        }

        div {
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #9A9A9A;
            letter-spacing: 0.06px;
            text-align: center;
            margin-top: 24px;
        }
    }

</style>
